* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    z-index: 2;
   
}

body {
    overflow-x: hidden;
    
    background-color: black;
    color: white;
}

.navbar{
    display: flex;
    align-items: center;
    justify-content: center;
}
.bar{
    text-decoration: none;
    list-style: none;
    display: flex;
    
}

.bar a{
    text-decoration: none;

    font-family: "Old Standard TT", serif;
  font-weight: 400;
  font-style: normal;
    color: white;
    margin-top: 50px;
    margin-left:120px;
    margin-right: 100px;
   font-size: large;
   letter-spacing: 2px;
    display: inline-block;
    transition: 0.4s ease-in-out;
}

.bar a:hover{    
        color: rgb(220, 242, 242); /* Cyan glow color */
        text-shadow: 0 0 10px  rgb(220, 242, 242), 0 0 20px  rgb(220, 242, 242), 0 0 40px  rgb(220, 242, 242);
}


.text{

    display: flex;
    display: block;
    align-items: center;
    justify-content: center;
    margin-top: 17%;
    font-family: "Old Standard TT", serif;
    font-weight: 400;
    font-style: normal;
    letter-spacing: 5px;
    
}
.first{
    text-align: center;
    letter-spacing: 2px;
    margin-top: 2%;
}

.text h1{   

    text-align: center;
    
    
    font-weight: 300px;
    font-size: 60px;
}


canvas {
    display: block;
    background-color: black;
}

.field {
    background-color: black;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: -1;
}


.canvas-container {
    background-color: rgb(30, 29, 29);
    position: relative;
    width: 100vw;
    height: 100vh;
    margin-top: 20vh; 
}

.field2 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    
    z-index: -1;
}

.button-container {
    position: absolute;
    top: 27%; 
    left: 8%; 
    
    display: flex;
    gap: 60px;
}


button {
    position: relative;
    font-size: 28px; 
    background: none;
    border: none;
    color: white;
    padding: 10px 20px;
    
}


button::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: -5px;
    width: 0;
    height: 2px;
    background-color: white;
    transition: width 0.3s ease-in-out, left 0.3s ease-in-out;
}

button.active::after {
    width: 100%;
    left: 0;
}


.statue_description{
    position: absolute;
    font-size: larger;
    top: 50%;
    left: 8%;
    width: 40%;
    height: 40px;
    line-height: 2;
}


