* {
  box-sizing: border-box;
  z-index: 2;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background-color: #000;
  overflow-x: hidden;
}

.navbar {
  justify-content: center;
  align-items: center;
  display: flex;
}

.bar {
  text-decoration: none;
  list-style: none;
  display: flex;
}

.bar a {
  color: #fff;
  letter-spacing: 2px;
  margin-top: 50px;
  margin-left: 120px;
  margin-right: 100px;
  font-family: Old Standard TT, serif;
  font-size: large;
  font-style: normal;
  font-weight: 400;
  text-decoration: none;
  transition: all .4s ease-in-out;
  display: inline-block;
}

.bar a:hover {
  color: #dcf2f2;
  text-shadow: 0 0 10px #dcf2f2, 0 0 20px #dcf2f2, 0 0 40px #dcf2f2;
}

.text {
  letter-spacing: 5px;
  justify-content: center;
  align-items: center;
  margin-top: 17%;
  font-family: Old Standard TT, serif;
  font-style: normal;
  font-weight: 400;
  display: block;
}

.first {
  text-align: center;
  letter-spacing: 2px;
  margin-top: 2%;
}

.text h1 {
  text-align: center;
  font-weight: 300px;
  font-size: 60px;
}

canvas {
  background-color: #000;
  display: block;
}

.field {
  z-index: -1;
  background-color: #000;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

.canvas-container {
  background-color: #1e1d1d;
  width: 100vw;
  height: 100vh;
  margin-top: 20vh;
  position: relative;
}

.field2 {
  z-index: -1;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.button-container {
  gap: 60px;
  display: flex;
  position: absolute;
  top: 27%;
  left: 8%;
}

button {
  color: #fff;
  background: none;
  border: none;
  padding: 10px 20px;
  font-size: 28px;
  position: relative;
}

button:after {
  content: "";
  background-color: #fff;
  width: 0;
  height: 2px;
  transition: width .3s ease-in-out, left .3s ease-in-out;
  position: absolute;
  bottom: -5px;
  left: 50%;
}

button.active:after {
  width: 100%;
  left: 0;
}

.statue_description {
  width: 40%;
  height: 40px;
  font-size: larger;
  line-height: 2;
  position: absolute;
  top: 50%;
  left: 8%;
}
